
import React from 'react';
import '../style/index.css';
import { NavLink } from 'react-router-dom';




function NavBar() {
  return(
    
      <div className='nav'>
      <div className='container-fluid nav-bg'>
          <div className='row'>
              <div className='col-10 mx-auto'>      
       <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
          <div className="container-fluid">
              <NavLink className="navbar-brand" to="/">
                  Portfolio Page 
              </NavLink>
              <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                 <span className="navbar-toggler-icon"></span>
              </button>


              <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                
                  <li className="nav-item">
                    <NavLink activeClassName='menu-active'
                      exact
                     className="nav-link" aria-current="page" to="/">
                        Home
                   </NavLink>
                  </li>

                  <li className="nav-item">
                    <NavLink activeClassName='menu-active' className="nav-link" to="/about">
                        About
                    </NavLink>
                  </li>

                  <li className="nav-item">
                    <NavLink activeClassName='menu-active' className="nav-link" to="/project">
                        Project
                    </NavLink>
                  </li>

                  <li className="nav-item">
                    <NavLink activeClassName='menu-active' className="nav-link" to="/skills">
                        Skills
                    </NavLink>
                  </li>

                  <li className="nav-item">
                    <NavLink activeClassName='menu-active' className="nav-link" to="/contact">
                        Contact
                    </NavLink>
                  </li> 
                  
              </ul>
              
              </div>
          </div>
      </nav> 
          </div>
       </div>
      </div>       
      </div>
  )
}

export default NavBar;



